import { gsap, Expo } from 'gsap'

export default class transition {
    constructor() {

        // Wait until the whole page is loaded.
        $(window).on("load", function () {
            hideLoad(); // call out animations.
        });
        
        // Transitions In
        // =================
        function revealLoad() {
            var tl_transitIn = gsap.timeline({ defaults: { 
                duration: 1, 
                ease: Expo.easeInOut 
                // ease: "Slow",
            }});
                tl_transitIn.set("#page-transition", { 
                    autoAlpha: 1 
                });
                tl_transitIn.to(".overlay", { 
                    scaleY: 1, 
                    // opacity: 1,
                    transformOrigin: "center bottom"
                }, 0);
                tl_transitIn.to(".preloader__transition", { 
                    autoAlpha: 1 
                }, 0.4);
        }
        
        // Transitions Out
        // ================
        function hideLoad() {
            var tl_transitOut = gsap.timeline({ defaults: { 
                duration: 1, 
                ease: Expo.easeInOut 
                // ease: "Slow",
            }});
                tl_transitOut.to(".preloader__transition", { 
                    autoAlpha: 0 
                });
                tl_transitOut.to(".overlay", { 
                    scaleY: 0,
                    // opacity: 0,
                    // transformOrigin: "center top"
                    transformOrigin: "center bottom"
                }, 0.3);

        }
        
        // On link click
        // ==============
        $("a")
            .not('[target="_blank"]') // omit from selection
            .not('[href^="#"]') // omit from selection
            .not('[href^="mailto"]') // omit from selection
            .not('[href^="tel"]') // omit from selection
            .not(".lg-trigger") // omit from selection
            .not(".no-transition") // omit from selection
            .on('click', function(e) {
            e.preventDefault();
        
            setTimeout(function (url) {
            window.location = url
            }, 1000, this.href);
        
            revealLoad(); // call in animations.
        });
  
    }
}