import { gsap, Power3 } from "gsap"
import { SplitText } from "gsap/SplitText"

gsap.registerPlugin(SplitText)

export default class navigation {
    constructor() {


        $('.hover').each(function(){
            gsap.set(this, {yPercent: 100})
        })
       
       $('a').hover(function(){
           let $text1 = new SplitText($(this).find('.inital'), {type:"words,chars"}), 
               chars1 = $text1.chars;
           let	$text2 = new SplitText($(this).find('.hover'), {type:"words,chars"}), 
               chars2 = $text2.chars;
           
           let tlHover = gsap.timeline({})
           .to(chars1, 0.5, {yPercent: -100, ease:  Power3.easeInOut}, 0.03)
           .to(chars2, 0.5, {yPercent: -100, ease:  Power3.easeInOut}, 0.03,.1);
           
           
       }, function(){
           let $text1 = new SplitText($(this).find('.inital'), {type:"words,chars"}), 
               chars1 = $text1.chars;
           let	$text2 = new SplitText($(this).find('.hover'), {type:"words,chars"}), 
               chars2 = $text2.chars;
           
           let tlHover = gsap.timeline({})
           .to(chars1, 0.5, {yPercent: 100, ease:  Power3.easeInOut}, 0.05)
           .to(chars2, 0.5, {yPercent: 100, ease:  Power3.easeInOut}, 0.05,0);
       })
        

    }
}